
<script>

    import connect from '@vkontakte/vk-connect';

    connect.send("VKWebAppInit", {});


export default {
    name: 'vue-main',
    props: {
        msg: String
    },
    data(){
        return {
            init_time:0,
            app_id : 7177213,
            group_id : 148056070,
            access_token:""
        }
    },

    mounted(){
        window.addEventListener("focus", function(event) {
            console.log("focus",event);
            if(new Date().getTime() - this.init_time<2000)return;
            window.location.reload();
            this.init_time=new Date().getTime();
        });


        console.log({connect});
        window.connect = connect;


        connect.subscribe(function(prm){
            console.log({prm});
        });


        connect.subscribe(({ detail: { type, data }}) => {
            console.log("onConnect",type,data);
            if (type === 'VKWebAppUpdateConfig') {
                const schemeAttribute = document.createAttribute('scheme');
                schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
                document.body.attributes.setNamedItem(schemeAttribute);
            }

            if (type === 'VKWebAppAccessTokenReceived') {
                this.access_token=data.access_token;
                window.token = data.access_token;
                this.init();
            }
        });

        this.get_access();


        this.fetchData();


    },

    computed:{
        hash(){
            try {
                let encoded = decodeURIComponent(window.location.hash.split("#")[1]);
                console.log({encoded});
                return encoded;
            }catch(err){
                console.log(err);
            }
            return null;
        }
    },

    methods: {

        async fetchData() {
            this.user = await connect.sendPromise('VKWebAppGetUserInfo');
        },

        async init() {
            let data = {"method": "groups.isMember", "request_id": "test22", "params": {"v":"5.102", "access_token":window.token, "group_id":this.group_id}};
            let isMember = await connect.sendPromise("VKWebAppCallAPIMethod",data);
            if(isMember.response===0) connect.send("VKWebAppJoinGroup", {"group_id": this.group_id});

        },

        async PayloadStorageUpload(payload) {
            let random = new Date().getTime()+Math.floor(Math.random()*100000);
            payload.random = random;
            /*Отправляем в группу с нашим random*/
            connect.send("VKWebAppSendPayload", {"group_id": this.group_id, payload});

            /*Ждем записи с нашим random*/
            return await new Promise((r, j)=>{
                let i = 1;
                async function check() {
                    i++;
                    let storage_get = await connect.sendPromise("VKWebAppCallAPIMethod",{"method": "storage.get", "request_id": random, "params": {"v":"5.102", "access_token":window.token, "key":"upload"}});
                    let json = JSON.parse(storage_get.response);
                    if(json.random === random) r(json);
                    else if(i>20) j("Result not finded!");
                    else
                        setTimeout(check, 350)
                }
                check();
            });
        },

        async poster() {
            if (!window.token) {this.get_access();return;}
            let upload_server = await connect.sendPromise("VKWebAppCallAPIMethod", {"method": "photos.getWallUploadServer", "request_id": "32test", "params": {"v": "5.102", "access_token": window.token}});
            let {upload_url, user_id} = upload_server.response;
            let upload = await this.PayloadStorageUpload({upload_url, user_id, photo: this.hash});
            let save = await connect.sendPromise("VKWebAppCallAPIMethod", {"method": "photos.saveWallPhoto", "request_id": "33test", "params": {"v": "5.102", "access_token": this.access_token, "user_id": user_id, photo: upload.photo, hash: upload.hash, server: upload.server, caption: "@art_privet(Арт за 5 сек)"}});
            let {id, owner_id} = save.response[0];
            let attachments = "photo" + owner_id + "_" + id;

            await connect.sendPromise("VKWebAppShowWallPostBox", {"message": "@art_privet(Арт за 5 сек)", attachments});
            connect.send("VKWebAppAddToFavorites", {});
        },

        get_access(){
            connect.send("VKWebAppGetAuthToken", {"app_id": this.app_id, "scope": "wall,photos"});
        }


    }
}
</script>



<template>
  <div class="main">


    {fetchedUser &&
    <div :style="{justifyContent: 'center', display:'flex',  padding:'10px', margin:'10px', alignItems: 'center'}">
    <img :style='{"width":"calc(95vmin)", "border-radius":"3%"}' alt='img' :src='hash'/>
  </div>
  }



    <v-btn   :click="poster" >
      Сохранить
    </v-btn>






  </div>
</template>
